<template>
    <div class="search">
        <div class="search-box">
            <img src="../../assets/search.png" class="search-pic">
            <input
                type="text"
                class="search-input"
                :placeholder="$t('base.searchKeywords')"
                @focus="goRoute"
                v-model="searchVal"
                @keyup.enter="search"
                ref='searchVal'>
            <img src="../../assets/clear-input.png" class="clear-input" v-if="searchVal.length > 0" @click="clear">
        </div>
    </div>
</template>

<script>

import { mapMutations } from 'vuex';
export default {
    props: ['isFocus'],
    data () {
        return {
            searchVal: '', // 搜索内容
            timeoutFlag: null // 防止刷接口
        };
    },
    methods: {
        ...mapMutations({
            setSearchListData: 'setSearchListData'
        }),
        // 搜索
        goRoute () {
            if (!this.isFocus) {
                this.$router.push({
                    path: '/search'
                });
            }
        },
        // 搜索
        search () {
            this.getSearchData(this.searchVal);
        },
        // 获取搜索数据
        getSearchData (val) {
            // 防止刷接口
            if (this.timeoutFlag != null) {
                clearTimeout(this.timeoutFlag);
            }
            this.timeoutFlag = setTimeout(() => {
                // 请求接口
                this.$emit('searchData', val);
            }, 1000);
        },
        // 清除input
        clear () {
            this.searchVal = '';
        }
    },
    mounted () {
        if (this.isFocus) {
            this.$refs.searchVal.focus();
        }
    },
    watch: {
        // 监听val变化， 实时存放，调用接口模糊搜索。
        searchVal (newVal, oldVal) {
            // 防止刷接口
            if (newVal === '') {
                this.setSearchListData([]);
                return;
            };
            this.getSearchData(newVal);
        }
    }
};
</script>

