    }

    .search-box {
        width: 100%;
        background: rgba(145,163,199,0.10);
        height: 100%;
        border-radius: 5px;
        display: flex;
        position: relative;

        .search-pic {
            margin: pxtorem(10px) pxtorem(8px) 0 pxtorem(12px);
            width: pxtorem(16px);
            height: pxtorem(16px);
            display: block;
        }
        .clear-input {
            display: block;
            width: pxtorem(14px);
            height: pxtorem(14px);
            position: absolute;
            right: pxtorem(5px);
            top: 50%;
            margin-top: pxtorem(-7px);
        }

        .search-input {
            flex: 1;
            display: block;
