// 滚动 翻页功能 mixin
export default {
    props: {
        data: [Array, Object]
    },
    data () {
        return {
            scrollHandler: null,
            scrollTop: 0,
            currPage: this.pageNum || 1
        };
    },
    computed: {
        dataEnd () {
            return (this.data.length * this.currPage) % 10 !== 0;
        }
    },
    methods: {
        init_scrollEvent () {
            this.remove_scrollEvent();
            this.scrollHandler = () => {
                if (this.dataEnd) {
                // TODO: 不能销毁事件，组件不去重新mounted，事件无法恢复
                // 旧逻辑：return this.remove_scrollEvent()
                    return;
                }
                let scrollTop =
            document.documentElement.scrollTop || document.body.scrollTop;
                let winHeight = window.innerHeight;
                let docHeight =
            document.body.scrollHeight || document.documentElement.scrollHeight;

                this.scrollTop = scrollTop;

                if (scrollTop + winHeight >= docHeight - 20) {
                    this.currPage++;
                    this.$emit('nextPage', this.currPage);
                    this.scrollEndHandler && this.scrollEndHandler(this.currPage);
                }
            };
            window.addEventListener('scroll', this.scrollHandler);
        },
        remove_scrollEvent () {
            window.removeEventListener('scroll', this.scrollHandler);
        },
        scrollEndHandler () {}
    },
    mounted () {
        this.init_scrollEvent();
    },
    beforeDestroy () {
        this.remove_scrollEvent();
    },
    watch: {
        pageNum (val) {
            this.currPage = val;
        },
        data (val) {
            this.currList = val || [];
        }
    }
};

