// 每一条数据
<template>
    <router-link :to="{ path: '/details', query: { postId: items.postId } }" tag="div" class="item-list">
        <div class="item-title">
            <div class="title">
                {{items.title}}
            </div>
            <div class="city" v-if="!items.checkStatus">
                {{items.area}}
            </div>
        </div>
        <div class="item-summary">
            {{items.description}}
        </div>
        <div :class="!items.checkStatus ? 'item-company' : 'item-time' ">
            <div class="left">
                <div class="company">
                    {{!items.checkStatus ? items.company : items.area}}
                </div>
                <div class="reputation" v-if="items.istrust === '1'">
                    {{$t('base.trusted')}}
                </div>
            </div>
            <div class="status">
                <span v-if="items.checkStatus == '0'" class="check">{{$t('details.approving')}}</span>
                <span v-else-if="items.checkStatus == '2'" class='no'>{{$t('details.failed')}}</span>
                <span v-else-if="items.checkStatus == '1'">
                    <template v-if="items.effectDay > 0 && items.effectDay <= 15">
                        <span class="day">{{items.effectDay}}</span>{{$t('details.day')}}
                    </template>
                    <template v-else-if="items.effectDay > 15">
                        <span class="time">{{$t('publish.time')}}</span>
                    </template>
                    <template v-else>
                        <span class="invalid">
                            {{$t('details.failure')}}
                        </span>
                    </template>
                </span>
            </div>
        </div>
    </router-link>
</template>

