<template>
    <div class="search-container">
        <Search :isFocus='1' @searchData='searchData'></Search>
        <div class="list">
            <base-list :data='searchListData'  v-if="searchListData.length > 0" @nextPage='nextPage'></base-list>
            <div class="empty-page" v-else-if='isShowEmpty && searchListData.length == 0'>
                <img src="../../assets/list-empty.png" class="empty-pic">
                <p  class="empty-text">
                    <span class="title">{{$t('empty.searchEmptyTop')}}</span>
                    <span>{{$t('empty.searchEmptyBottom')}}</span>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
import { publicPost } from '../../js/api/index.js';
import getApiUrl from '../../js/api/apiUrlList';
import Search from '../../components/common/search.vue';
import BaseList from '../../components/common/list';
import { mapState, mapMutations } from 'vuex';
export default {
    name: 'search',
    data () {
        return {
            postData: {
                keyword: '',
                pageNo: 1,
                pageSize: 10
            },
            isShowEmpty: false
        };
    },
    created () {
        this.setSearchListData([]);
        this.$router.setTitle(this.$t('title.search'));
    },
    computed: {
        ...mapState({
            searchListData: state => state.home.searchListData
        })
    },
    components: {
        Search,
        BaseList
    },
    methods: {
        ...mapMutations({
            setSearchListData: 'setSearchListData'
        }),
        // 获取数据
        getData () {
            this.isShowEmpty = true;
            publicPost(getApiUrl('getSearchList'), this.postData).then((res) => {
                if (this.postData.pageNo === 1) {
                    this.setSearchListData(res);
                } else {
                    this.setSearchListData(this.searchListData.concat(res));
                }
            }).catch(() => {
                console.warn('error');
            });
        },
        // 搜索
        searchData (val) {
            this.postData = {
                keyword: val,
                pageNo: 1,
                pageSize: 10
            };
            this.getData();
        },
        // 获取下一页数据
        nextPage (page) {
            this.postData.pageNo = page;
            this.getData();
        }
    }
};
</script>

<style scoped>
    .search-container {
        background: #fff;
        height: 100%;
    }
</style>
